import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { AlertController, Platform } from '@ionic/angular';
import { Subscription } from 'rxjs';
import { AuthConstants } from 'src/app/configuration/auth-constants';
import { IBatch } from 'src/app/models/IBatch';
import { IProduct } from 'src/app/models/IProduct';
import { IStats } from 'src/app/models/IStats';
import { IUnit } from 'src/app/models/IUnit';
import { IWeightLimits } from 'src/app/models/IWeightLimits';
import { IWSMessagge } from 'src/app/models/IWSMessage';
import { AuthService } from 'src/app/services/auth.service';
import { HttpService } from 'src/app/services/http.service';
import { StorageService } from 'src/app/services/storage.service';
import { environment } from './../../configuration/enviroment';

@Component({
  selector: 'app-home',
  templateUrl: './home.page.html',
  styleUrls: ['./home.page.scss'],
})
export class HomePage implements OnInit, OnDestroy {

  timeout: any;
  ws: WebSocket = null;
  connection_state: boolean = false;
  stats: IStats = {OK: 0, NOK: 0, Total:0, percentageOK: 0, percentageNOK: 0, percentagesReason:[]};
  msg: IWSMessagge;
  weight: number = 0;
  result: number = 0;
  limits: IWeightLimits = {max:0, min:0};
  userName: string  ="";
  userSubs: Subscription;
  batch: IBatch;
  product: IProduct;
  units: IUnit[];
  uri: string;
  platforms: string[];
  plat: string;

  constructor(
    private storageService: StorageService,
    private authService: AuthService,
    private alertController: AlertController,
    private router: Router,
    private httpClient: HttpService,
    public platform: Platform
  ) { }

  ngOnInit() {
    console.log("home loaded");
    this.userSubs = this.storageService.userData.subscribe(user=>{
      console.log(user);
      if(user === null){
        this.userName = "Sin usuario";
      } else {
        this.userName = user.firstName + " " + user.lastName;
      }
    });
    this.platforms = this.platform.platforms();
    for (let index = 0; index < this.platforms.length; index++) {
      if (this.platforms[index] == 'mobileweb' || this.platforms[index] == 'pwa') {
        this.plat = 'web';
        break;
      }
    }
    this.storageService.get(AuthConstants.USER).then(res=>{
      console.log(res); //no hace nada el dato viene por la subscripcion
    });
    if(this.plat === 'web'){
      this.uri = environment.ws_local
    } else {
      this.uri = environment.ws_mobile
    }
    this.createWebSocket();
    this.getActivePO();
  }

  ngOnDestroy(){
    this.userSubs.unsubscribe();
  }

  ionViewWillLeave() {
    this.closeWebSocket();
    console.log("home will leave");
  }

  createWebSocket() {
    this.ws = new WebSocket(this.uri);
    console.log('create WS');
    
    this.ws.onopen = (()=>{
      console.log('open WS');
      this.connection_state = true;
      if(this.timeout !== undefined){
        console.log("clear timeout");
        clearTimeout(this.timeout);
      }
    });
  
    this.ws.onmessage= ((msg)=>{
      this.msg = JSON.parse(msg.data);
      console.log(this.msg);
      this.handleMessageWS();
    });
  
    this.ws.onclose = (()=>{
      console.log('closed ev WS');
      this.connection_state = false;
      this.timeout = setTimeout(() => {
        console.log("set timeout ws");
        this.createWebSocket();
      }, 5000);
    });
  }
  
  sendWebSocket(message: string) {
    this.ws.send(message);
  }
  
  closeWebSocket() {
      console.log('close WS');
      this.connection_state = false;
      if(this.ws !== null) {
        this.ws.close();
      }
  }

  handleMessageWS() {
    console.log(this.msg.payload);
    switch(this.msg.command){
      case 'stats':
        console.log('stats');
        this.stats = this.msg.payload.stats;
        break;
      case 'weight': 
        console.log('weight');
        this.weight = this.msg.payload.weight.weight;
        this.result = this.weight > 89 ? 1:0;
        this.getActivePO();
        break;
      case 'limits': 
        console.log('limits');
        this.limits = this.msg.payload.limits;
        break;
      default:
          break;
    }
  }

  async logout(){
    const alert = await this.alertController.create({
      header:'Atención!',
      message: "Desea cerrar la sesión?",
      buttons: [
        {
          text:'No',
          role:'cancel',
          handler: ()=>{

          }
      },{
          text:'OK',
          handler: ()=>{
            this.authService.logout().then(()=>this.router.navigate(['/home']));
          }
      }]
    });
    await alert.present();
  }

  getActivePO(){
    this.httpClient.getActivePO().subscribe(data=>{
      this.batch = data[0].products[0].batches.filter(batch=>batch.state==1)[0];
      this.product = data[0].products[0];
      this.limits.max = this.product.weightMax;
      this.limits.min = this.product.weightMin;
      this.stats.OK = this.batch.okTotal;
      this.stats.NOK = this.batch.nokTotal;
      this.stats.Total = this.batch.okTotal +  this.batch.nokTotal;
      this.stats.percentageNOK = (this.stats.NOK / this.stats.Total) * 100;
      this.stats.percentageOK = (this.stats.OK / this.stats.Total) * 100;
      this.stats.percentagesReason[0] = (this.batch.nokHigh / this.stats.NOK) * 100;
      this.stats.percentagesReason[1] = (this.batch.nokLow / this.stats.NOK) * 100;
      this.stats.percentagesReason[2] = (this.batch.nokOther/ this.stats.NOK) * 100;
      this.getUnits(15, this.batch.id);
    });
  }

  getUnits(units: number, batchId: number){
    this.httpClient.getUnits(units, batchId).subscribe(data=>{
      console.log(data)
      this.units = data;
    });
  }

}
